(function() {
  'use strict';

  angular
  .module('wizweekPy')
  .service('api', api);

  /** @ngInject */
  function api(apiBaseUrl, auth, $http) {
    var service = {
      ping: ping,
      put: put,
      get: get,
      request: request
    };
    return service;

    // This is helpful since I currently run the API on a free heroku dyno
    // (so this ping call will wake up the API when the app is first loaded).
    function ping() {
      get('');
    }

    function get(url) {
      return request('GET', url);
    }

    function put(url, data) {
      return request('PUT', url, data);
    }

    function request(method, url, data) {
      return $http({
        method: method,
        url: apiBaseUrl + url,
        data: data,
        headers: { 'Authorization': 'Bearer ' + auth.token }
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('wizweekPy')
    .service('optimizer', optimizer);

  /** @ngInject */
  function optimizer(optimizeApi) {
    var DAYS_IN_WEEK = 7;

    this.exec = exec;

    function exec(tasks, tasksCalInfo, settings, appointments, start, end) {
      var request = {
        timeZone: tasksCalInfo.timeZone,
        weeklyTaskBlocks: settingsTaskBlocks(settings),
        appointments: appointments.map(formatAppointment),
        tasks: tasks.map(formatTask),
        startTaskSchedule: start.toISOString(),
        endTaskSchedule: end.toISOString()
      };
      return optimizeApi.optimize(request);
    }

    function formatAppointment(appointment) {
      return {
        title: appointment.summary,
        start: appointment.start.dateTime,
        end: appointment.end.dateTime
      };
    }

    function formatTask(task) {
      var formatted = {
        title: task.title,
        estimatedHours: parseFloat(task.hours),
        reward: parseFloat(task.value)
      };
      if (task.deadline) {
        formatted.deadline = task.deadline;
      }
      if (task.minStart) {
        formatted.startOnOrAfter = task.minStart;
      }
      return formatted;
    }

    function settingsTaskBlocks(settings) {
      var taskBlocks = [];
      for (var i = 0; i < DAYS_IN_WEEK; i++) {
        var start = settings.workStartTimes[i];
        var end = settings.workEndTimes[i];
        if (start.getTime() == end.getTime()) {
          taskBlocks.push([]);
        } else {
          taskBlocks.push([{
            start: hoursAndMinutes(start), end: hoursAndMinutes(end)
          }]);
        }
      }
      return taskBlocks;
    }

    function hoursAndMinutes(date) {
      return date.getHours() + ':' + date.getMinutes();
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('wizweekPy')
    .service('optimizeApi', optimizeApi);

  /** @ngInject */
  function optimizeApi(optimizeUrl, $http) {
    var service = {
      ping: ping,
      optimize: optimize
    };
    return service;

    function ping() {
      return $http.get(optimizeUrl);
    }

    function optimize(request) {
      return $http.post(optimizeUrl, request).then(function(response) {
        return response.data;
      });
    }
  }
})();

(function() {
  'use strict';

  angular
  .module('wizweekPy')
  .service('optimizeAndSync', optimizeAndSync);

  /** @ngInject */
  function optimizeAndSync(gcal, settingsStore, optimizer, $q, eventSync) {
    var service = {
      exec: exec
    };
    return service;

    function exec(tasks, msgCallback, doneCallback) {
      var settings, appointments, tasksCalInfo, optimizedEvents, currentEvents;
      var taskEventsPromise, optimizePromise;

      var curTime = new Date();
      var scheduleStart = new Date(
        curTime.getFullYear(), curTime.getMonth(), curTime.getDate(),
        curTime.getHours() + 1
      );
      var scheduleEnd = new Date(scheduleStart);
      scheduleEnd.setDate(scheduleStart.getDate() + 90);

      loadSettings();

      function loadSettings() {
        msgCallback('Loading settings...');
        settingsStore.load().then(function(loadedSettings) {
          settings = loadedSettings;
          loadEvents();
        });
      }

      function loadEvents() {
        loadAppointments();

        // Start loading the task events for the range right away, but also
        // track its promoise so we can wait until it's done.
        taskEventsPromise = gcal.events(
          settings.tasksCalId, scheduleStart, scheduleEnd
        );
      }

      function loadAppointments() {
        msgCallback('Loading appointments...');
        var apptsPromise =
          gcal.events(settings.appointmentsCalId, scheduleStart, scheduleEnd);

        var infoPromise = gcal.calendarInfo(settings.tasksCalId);

        $q.all([apptsPromise, infoPromise]).then(function(results) {
          appointments = gcal.rejectAllDayEvents(results[0]);
          tasksCalInfo = results[1];
          runOptimizer();
        });
      }

      function runOptimizer() {
        msgCallback('Running optimizer...');
        optimizePromise = optimizer.exec(
          tasks, tasksCalInfo, settings, appointments, scheduleStart, scheduleEnd
        );
        $q.all([taskEventsPromise, optimizePromise]).then(function(results) {
          currentEvents = results[0];
          optimizedEvents = results[1];
          syncEvents();
        }, function() {
          msgCallback('No feasible schedule found. Please check your tasks.');
          doneCallback();
        });
      }

      function syncEvents() {
        msgCallback('Syncing events to tasks calendar...');
        eventSync.syncEvents(settings.tasksCalId, currentEvents, optimizedEvents).
          then(function() {
            msgCallback('Optimized schedule has been synced to your tasks calendar.');
            doneCallback();
          });
      }
    }
  }
})();

(function() {
  'use strict';

  angular
  .module('wizweekPy')
  .service('gcal', gcal);

  function gcal(GApi) {
    var service = {
      calendarInfo: calendarInfo,
      events: events,
      eventUpdatesBatch: eventUpdatesBatch,
      rejectAllDayEvents: rejectAllDayEvents
    };
    return service;

    function events(calId, timeMin, timeMax) {
      var path = calPath(calId) + "/events?timeMin=" + timeMin.toISOString() +
        "&timeMax=" + timeMax.toISOString();

      return GApi.request(path).then(function(resp) {
        return resp.result.items;
      });
    }

    function eventUpdatesBatch(calId, eventUpdates) {
      var requests = eventUpdates.map(function(update) {
        var path = calPath(calId) + '/events';
        if (update.id) {
          path += '/' + update.id;
        }
        return { path: path, method: update.method, body: update.data };
      });
      return GApi.batch(requests);
    }

    function calendarInfo(calId) {
      return GApi.request(calPath(calId)).then(function(resp) {
        return resp.result;
      });
    }

    function calPath(calId) {
      return 'calendar/v3/calendars/' + calId;
    }

    function rejectAllDayEvents(events) {
      return events.filter(isNotAllDayEvent, events);
    }

    function isNotAllDayEvent(event) {
      return event['start']['dateTime'];
    }
  }
})();

(function() {
  'use strict';

  angular
  .module('wizweekPy')
  .service('eventSync', eventSync);

  /** @ngInject */
  function eventSync(gcal) {
    var service = {
      syncEvents: syncEvents
    };
    return service;

    // Note that the oldEvents and newTasks have slightly different formats.
    // This is due to the fact that oldEvents comes from the Google calendar
    // api, whereas newTasks comes from the schedule optimizer service.
    function syncEvents(tasksCalId, oldEvents, newTasks) {
      var requests = [];
      for (var i = 0, len = newTasks.length; i < len; i++) {
        if (i < oldEvents.length) {
          if (!eventAndTaskMatch(oldEvents[i], newTasks[i])) {
            requests.push(updateRequest(oldEvents[i], newTasks[i]));
          }
        } else {
          requests.push(createRequest(newTasks[i]));
        }
      }
      for (var k = newTasks.length; k < oldEvents.length; k++) {
        requests.push(deleteRequest(oldEvents[k]));
      }

      return gcal.eventUpdatesBatch(tasksCalId, requests);
    }

    function updateRequest(oldEvent, newTask) {
      return { method: 'PUT', id: oldEvent.id, data: taskData(newTask) };
    }

    function createRequest(newTask) {
      return { method: 'POST', data: taskData(newTask) };
    }

    function deleteRequest(oldEvent) {
      return { method: 'DELETE', id: oldEvent.id };
    }

    function taskData(newTask) {
      return {
        summary: newTask.title, start: { dateTime: newTask.start },
        end: { dateTime: newTask.end }
      };
    }

    function eventAndTaskMatch(oldEvent, newTask) {
      return oldEvent.summary == newTask.title &&
        dateStrsMatch(oldEvent.start.dateTime, newTask.start) &&
          dateStrsMatch(oldEvent.end.dateTime, newTask.end);
    }

    function dateStrsMatch(str1, str2) {
      return new Date(str1).getTime() == new Date(str2).getTime();
    }
  }
})();

