/*global angular */

/**
 * Directive that places focus on the element it is applied to when the
 * expression it binds to evaluates to true
 */
angular.module('wizweekPy')
	.directive('todoFocus', function todoFocus($timeout) {
		'use strict';

		return function (scope, elem, attrs) {
			scope.$watch(attrs.todoFocus, function (newVal) {
				if (newVal) {
					$timeout(function () {
						elem[0].focus();
					}, 0, false);
				}
			});
		};
	});

/*global angular */

/**
 * Directive that executes an expression when the element it is applied to gets
 * an `escape` keydown event.
 */
angular.module('wizweekPy')
	.directive('todoEscape', function () {
		'use strict';

		var ESCAPE_KEY = 27;

		return function (scope, elem, attrs) {
			elem.bind('keydown', function (event) {
				if (event.keyCode === ESCAPE_KEY) {
					scope.$apply(attrs.todoEscape);
				}
			});

			scope.$on('$destroy', function () {
				elem.unbind('keydown');
			});
		};
	});

/**
 * The main controller for the app. The controller:
 * - retrieves and persists the model via the todoStorage service
 * - exposes the model to the template and provides event handlers
 */
angular.module('wizweekPy')
	.controller('TodoController', function TodoController($filter, store, optimizeAndSync) {
		'use strict';

    var vm = this;

    store.get();
		var todos = vm.todos = store.todos;

    var blankNewTodo = {
      title: '', hours: null, value: null, deadline: null, minStart: null
    }

    vm.optimizing = false;
    vm.optimizeMessage = '';
    vm.optimize = function() {
      optimizeAndSync.exec(vm.todos, function(message) {
        vm.optimizeMessage = message;
      }, function() {
        vm.optimizing = false;
      });
    };

    vm.deadlinePopupOpen = false;
    vm.deadlineFocused = function() {
      vm.deadlinePopupOpen = true;
    }

    vm.minStartPopupOpen = false;
    vm.minStartFocused = function() {
      vm.minStartPopupOpen = true;
    }

    vm.newTodo = angular.extend({}, blankNewTodo);
		vm.editedTodo = null;

		vm.addTodo = function() {
			var newTodo = {
				title: vm.newTodo.title.trim(),
        hours: vm.newTodo.hours,
        value: vm.newTodo.value,
        deadline: vm.newTodo.deadline,
        minStart: vm.newTodo.minStart,
				completed: false
			};

			if (!newTodo.title || !newTodo.hours || !newTodo.value) {
				return;
			}

			vm.saving = true;
			store.insert(newTodo)
				.then(function success() {
					vm.newTodo = angular.extend({}, blankNewTodo);
				})
				.finally(function () {
					vm.saving = false;
				});
		};

		vm.editTodo = function (todo) {
			vm.editedTodo = todo;
			// Clone the original todo to restore it on demand.
			vm.originalTodo = angular.extend({}, todo);
		};

		vm.saveEdits = function (todo, event) {
			// Blur events are automatically triggered after the form submit event.
			// This does some unfortunate logic handling to prevent saving twice.
			if (event === 'blur' && vm.saveEvent === 'submit') {
				vm.saveEvent = null;
				return;
			}

			vm.saveEvent = event;

			if (vm.reverted) {
				// Todo edits were reverted-- don't save.
				vm.reverted = null;
				return;
			}

			todo.title = todo.title.trim();

			if (todo.title === vm.originalTodo.title) {
				vm.editedTodo = null;
				return;
			}

			store[todo.title ? 'put' : 'delete'](todo)
				.then(function success() {}, function error() {
					todo.title = vm.originalTodo.title;
				})
				.finally(function () {
					vm.editedTodo = null;
				});
		};

		vm.revertEdits = function (todo) {
			todos[todos.indexOf(todo)] = vm.originalTodo;
			vm.editedTodo = null;
			vm.originalTodo = null;
			vm.reverted = true;
		};

		vm.removeTodo = function (todo) {
			store.delete(todo);
		};

		vm.saveTodo = function (todo) {
			store.put(todo);
		};

		vm.toggleCompleted = function (todo, completed) {
			if (angular.isDefined(completed)) {
				todo.completed = completed;
			}
			store.put(todo, todos.indexOf(todo))
				.then(function success() {}, function error() {
					todo.completed = !todo.completed;
				});
		};

		vm.clearCompletedTodos = function () {
			store.clearCompleted();
		};

		vm.markAll = function (completed) {
			todos.forEach(function (todo) {
				if (todo.completed !== completed) {
					vm.toggleCompleted(todo, completed);
				}
			});
		};
	});

(function() {
  'use strict';
  angular.module('angular-gapi', []);
})();

(function() {
  'use strict';

  angular
  .module('angular-gapi')
  .service('GAuth', GAuth);

  /** @ngInject */
  function GAuth(GApi, $q) {
    var config;
    var cachedAuth2 = null;
    var initialized = false;

    var service = {
      setConfig: setConfig,
      checkAuth: checkAuth,
      signIn: signIn,
      signOut: signOut
    };
    return service;

    // Takes an object like this:
    // {
    //   clientId: 'xyz.apps.googleusercontent.com',
    //   scope: 'https://www.googleapis.com/auth/calendar',
    //   currentUserListener: function(currentUser) { ... }
    // }
    function setConfig(conf) {
      config = conf;
    }

    function signIn() {
      init().then(function(auth2) {
        auth2.getAuthInstance().signIn();
      });
    }

    function signOut() {
      init().then(function(auth2) {
        auth2.getAuthInstance().signOut();
      });
    }

    function checkAuth() {
      // init() checks the authentiation but by just calling it (but not
      // returning it) we avoid exposing promise that would return the auth2 instance.
      init();
    }

    function init() {
      if (initialized) {
        return get();
      } else {
        return get().then(function(auth2) {
          auth2.init({ client_id: config.clientId, scope: config.scope });
          auth2.getAuthInstance().currentUser.listen(config.currentUserListener);
          initialized = true;
          return auth2;
        });
      }
    }

    function get() {
      if (cachedAuth2) {
        return $q.resolve(cachedAuth2);
      } else {
        return GApi.get().then(function(gapi) {
          cachedAuth2 = gapi.auth2;
          return gapi.auth2;
        });
      }
    }
  }
})();

(function() {
  'use strict';

  angular
  .module('angular-gapi')
  .service('GApi', GApi);

  /** @ngInject */
  function GApi($q, $document, $window, $timeout) {
    var gapiLoaded = false;
    var gapiLoading = false;
    var observerCallbacks = [];

    this.get = get;
    this.request = request;
    this.batch = batch;

    function batch(requests) {
      if (requests.length == 0) {
        return $q.resolve();
      }

      return get().then(function(gapi) {
        var batch = gapi.client.newBatch();
        requests.forEach(function(req) {
          batch.add(gapi.client.request(req));
        })
        return batch;
      });
    }

    function request(args) {
      return get().then(function(gapi) {
        return gapi.client.request(args);
      });
    }

    function get() {
      if (gapiLoaded) {
        return $q.when($window.gapi);
      } else {
        var deferred = $q.defer();
        observerCallbacks.push(deferred);
        if (!gapiLoading) {
          gapiLoading = true;
          loadWithClientAndAuth().then(function(gapi) {
            gapiLoaded = true;
            gapiLoading = false;
            observerCallbacks.forEach(function(c) { c.resolve(gapi); });
          });
        }
        return deferred.promise;
      }
    }

    function loadWithClientAndAuth() {
      var deferred = $q.defer();
      loadScript().then(function(gapi) {
        gapi.load('client:auth2', function() {
          deferred.resolve(gapi);
        });
      });
      return deferred.promise;
    }

    function loadScript() {
      var URL = 'https://apis.google.com/js/api.js?onload=_gapiOnLoad';

      var deferred = $q.defer();
      $window._gapiOnLoad = function(){
        deferred.resolve($window.gapi);
      }
      var script = $document[0].createElement('script');
      script.onerror = function (e) {
        $timeout(function () {
          deferred.reject(e);
        });
      };
      script.src = URL;
      $document[0].body.appendChild(script);
      return deferred.promise;
    }
  }
})();

(function() {
  'use strict';

  angular
  .module('wizweekPy')
  .run(runBlock);

  /** @ngInject */
  function runBlock(auth, apiPing) {
    auth.checkAuth();

    // Just ping the API to wake it up (it runs on a free Heroku dyno)
    apiPing.ping();
  }
})();

(function() {
  'use strict';

  angular
    .module('wizweekPy')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
    .state('main',  {
      abstract: true,
      templateUrl: 'app/layout/main.html',
      controller: 'MainController',
      controllerAs: 'main'
    })
    .state('main.authorized', {
      abstract: true,
      templateUrl: 'app/layout/authorized.html',
      controller: 'AuthorizedController',
      controllerAs: 'authorized'
    })
    .state('main.authorized.dashboard', {
      url: '/',
      templateUrl: 'app/dashboard/dashboard.html',
      controller: 'TodoController',
      controllerAs: 'dash'
    })
    .state('main.authorized.settings', {
      url: '/settings',
      templateUrl: 'app/settings/settings.html',
      controller: 'SettingsController',
      controllerAs: '$ctrl'
    })

    $urlRouterProvider.otherwise('/');
  }

})();

